import { Component, OnInit } from '@angular/core';
import { QuizService } from '../quiz.service';

@Component({
  selector: 'quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.css']
})
export class QuizComponent implements OnInit {

  questions: any;
  userAnswers: any = [false, false, false, false, false, false, false, false, false, false];

  constructor(private quizService: QuizService) { 
    this.quizService.getQuestions().subscribe(response => {
      this.questions = response;
    });

  }

  ngOnInit() {
  }

  checkAnswer(choice, answer, index) {
    if(choice == answer) {
      this.userAnswers[index] = true;
    } else {
      this.userAnswers[index] = false;
    }
  }

  calculateScore(form) {
    let scores = 0;
    for (let i = 0; i < this.userAnswers.length; i++) {
      if (this.userAnswers[i] == true) {
        scores++;
      } 
    } 
    console.log(this.userAnswers, scores);
        this.quizService.addScores({...form.value, scores}).subscribe(response => {
        this.questions = response;
      });
  }


}